"use client";
import { useEffect, useMemo, useState } from 'react';
import { useParams } from 'next/navigation';
import { motion, AnimatePresence } from 'framer-motion';

type NodeState = {
  node: { id: string; text: string; choices: string[] };
  images: { landscape?: string; portrait?: string };
};

export default function Play() {
  const params = useParams();
  const sessionId = params?.id as string;
  const [state, setState] = useState<NodeState | null>(null);
  const [loadingChoice, setLoadingChoice] = useState<string | null>(null);

  async function load() {
    const res = await fetch(`/api/story/state?sessionId=${sessionId}`);
    const json = await res.json();
    if (!res.ok) return;
    setState(json);
  }

  useEffect(() => { load(); }, [sessionId]);

  async function choose(choice: string) {
    setLoadingChoice(choice);
    const res = await fetch('/api/story/continue', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ sessionId, choice }),
    });
    await res.json();
    setLoadingChoice(null);
    load();
  }

  return (
    <div className="px-4 sm:px-6 py-6 max-w-3xl mx-auto">
      {!state ? (
        <div className="text-center opacity-70">Loading…</div>
      ) : (
        <div className="flex flex-col gap-4">
          <div className="w-full overflow-hidden rounded-2xl bg-white/70 dark:bg-white/5 border border-black/5 dark:border-white/10 shadow-sm">
            <picture>
              {state.images.portrait && (
                <source media="(orientation: portrait)" srcSet={state.images.portrait} />
              )}
              {state.images.landscape && (
                <img src={state.images.landscape} alt="Scene" className="w-full h-auto block" />
              )}
            </picture>
          </div>

          <AnimatePresence mode="wait">
            <motion.div
              key={state.node.id}
              initial={{ opacity: 0, y: 8 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -8 }}
              transition={{ duration: 0.25 }}
              className="rounded-2xl p-5 bg-white/80 dark:bg-white/5 backdrop-blur border border-black/5 dark:border-white/10"
            >
              <p className="leading-7">{state.node.text}</p>
            </motion.div>
          </AnimatePresence>

          <div className="grid gap-3 sm:grid-cols-3">
            {state.node.choices.slice(0,3).map((c, i) => (
              <button
                key={i}
                onClick={() => choose(c)}
                className="rounded-xl p-[2px] bg-gradient-to-br from-cyan-400/60 via-fuchsia-400/40 to-violet-500/60 hover:from-cyan-400 hover:to-violet-500 transition"
                disabled={!!loadingChoice}
              >
                <div className="rounded-[10px] bg-white/80 dark:bg-black/40 backdrop-blur-md py-3 px-4 text-sm text-center min-h-12 grid place-items-center">
                  {loadingChoice === c ? '…' : c}
                </div>
              </button>
            ))}
          </div>
        </div>
      )}
    </div>
  );
}

